    </svg>
    <div class="navbar">
      <div class="navbar-left-container">
        <a href="/">
          <img class="navbar-brand-logo" src="../../static/logo.png" />
        </a>
      </div>
      <div class="navbar-right-container" style="display: flex;">
        <div class="navbar-menu-container">
          <!--<a href="/" class="navbar-link">我的账户</a>-->
          <span class="navbar-link"></span>
          <a href="javascript:void(0)" class="navbar-link" v-show="!!loginUser">{{userName}}</a>
          <a
            href="javascript:void(0)"
            class="navbar-link"
            v-on:click="loginState=false"
            v-show="!loginUser"
          >登录</a>
          <a
            href="javascript:void(0)"
            class="navbar-link"
            v-on:click="logout"
            v-show="!!loginUser"
          >退出</a>
          <div class="navbar-cart-container" v-show="!!loginUser">
            <span class="navbar-cart-count">{{userGoodsCount}}</span>
            <a class="navbar-link navbar-cart-link" href="/#/userCart">
              <svg class="navbar-cart-logo">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-cart" />
              </svg>
            </a>
          </div>
        </div>
      </div>
    </div>
    <div class="md-modal modal-msg md-modal-transition md-show">
      <div class="md-modal-inner" v-show="!loginState">
        <div class="md-top">
          <div class="md-title">账号登录</div>
          <button class="md-close" v-on:click="loginState=true">Close</button>
        </div>
        <div class="md-content">
          <div class="confirm-tips">
            <div class="error-wrap">
              <span class="error error-show" v-show="errTips">用户名或者密码错误</span>
            </div>
            <form action="#">
              <ul>
                <li class="regi_form_input">
                  <i class="icon IconPeople"></i>
                  <input
                    type="text"
                    tabindex="1"
                    name="loginname"
                    v-model="userName"
                    class="regi_login_input regi_login_input_left"
                    placeholder="用户名"
                  />
                </li>
                <li class="regi_form_input noMargin">
                  <i class="icon IconPwd"></i>
                  <input
                    type="password"
                    tabindex="2"
                    name="password"
                    v-model="userPwd"
                    class="regi_login_input regi_login_input_left login-input-no input_text"
                    placeholder="密码"
                    autocomplete="pwd"
                  />
                </li>
              </ul>
            </form>
          </div>
          <div class="login-wrap">
            <a href="javascript:;" class="btn-login" v-on:click="getLogin">登 录</a>
          </div>
        </div>
      </div>
    </div>
    <div class="md-overlay" v-show="!loginState"></div>
  </header>
</template>


<script>
import axios from "axios";
import getGoodsCount from "../utils/getCartCount";
import { mapState, mapActions } from "vuex";
import CryptoJS from "crypto-js";

export default {
  //保存用户信息
  data() {
    return {
      userName: "",
      userPwd: "",
      loginUser: "",
      userId: "",
      errTips: false,
      loginState: true,
      userCartList: []
    };
  },

  computed: {
    ...mapState(["userGoodsCount"])
  },

  mounted: function() {
    this.checkLogin();
    let num = localStorage.getItem("userGoodsCount");
    this.setUserGoodsCount(num);
  },

  methods: {
    ...mapActions(["setUserGoodsCount"]),
    getLogin() {
      let userInfo = {
        userName: this.userName,
        userPwd: CryptoJS.MD5(this.userPwd).toString()
      };
      axios.post("/users/login", userInfo).then(res => {
        if (res.data.statusCode === 1 && res.data.result) {
          //1.1 从后端获取用户名后赋值给loginUser，要么为空，要么有用户名
          this.loginUser = res.data.result.userName;
          this.userId = res.data.result.userId;
          //1.2控制用户名密码错误提示是否显示
          this.errTips = false;
          this.loginState = true;
          getGoodsCount(axios, this.setUserGoodsCount);
        } else {
          this.errTips = true;
          this.loginState = false;
        }
      });
    },
    logout() {
      //1.3登出，将登录用户信息清空，同时更改loginState，并将路由指向首页
      axios
        .get("/users/logout")
        .then(res => {
          if (res.data.statusCode === 1) {
            this.loginUser = res.data.result;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    checkLogin: function() {
      axios.get("/users/checkLogin").then(res => {
        if (res.data.statusCode === 1 && res.data.result) {
          this.loginUser = res.data.result;
          this.userName = res.data.result;
          this.errTips = false;
          this.loginState = true;
        } else if (res.data.statusCode === 0 && res.data.msg === "请登录") {
          this.loginUser = "";
          this.userName = "";
        }
      });
    }
    // ,
    // async getGoodsCount() {
    //     const {data:{result}} = await axios.get('/users/cartList')
    //     let count = 0
    //     result.forEach((item) => {
    //         if (item.checked) {
    //             count += parseInt(item.productNum)
    //         }
    //     })
    //     this.setUserGoodsCount(count)
    //     localStorage.setItem("userGoodsCount", count);
    // }
  }
};
</script>


<style>
</style>



