<template>
  <div>
    <nav-header></nav-header>
    <nav-bread>
      <span slot="bread">购物车</span>
    </nav-bread>

    <svg
      style="position: absolute; width: 0; height: 0; overflow: hidden;"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
    >
      <defs>
