          />
        </symbol>
        <symbol id="icon-edit" viewBox="0 0 32 32">
          <title>edit</title>
          <path
            class="path1"
            d="M28.287 8.51l-4.805-4.806 0.831-0.831c0.472-0.472 1.086-0.777 1.564-0.777 0.248 0 0.452 0.082 0.622 0.253l3.143 3.144c0.539 0.54 0.133 1.529-0.524 2.186l-0.831 0.831zM26.805 9.992l-1.138 1.138-4.805-4.806 1.138-1.138 4.805 4.806zM24.186 12.612l-14.758 14.762-4.805-4.806 14.758-14.762 4.805 4.806zM7.379 28.288l-4.892 1.224 1.223-4.894 3.669 3.67zM31.123 4.011l-3.143-3.144c-0.567-0.567-1.294-0.867-2.103-0.867-1.036 0-2.174 0.52-3.045 1.391l-20.429 20.436c-0.135 0.134-0.23 0.302-0.276 0.487l-2.095 8.385c-0.089 0.355 0.017 0.736 0.276 0.995 0.198 0.198 0.461 0.307 0.741 0.307 0.085 0 0.171-0.010 0.254-0.031l8.381-2.096c0.185-0.047 0.354-0.142 0.487-0.276l20.43-20.436c1.409-1.41 2.042-3.632 0.524-5.15v0z"
          />
        </symbol>
        <symbol id="icon-del" viewBox="0 0 32 32">
          <title>delete</title>
          <path
            class="path1"
            d="M11.355 4.129v-2.065h9.29v2.065h-9.29zM6.194 29.935v-23.742h19.613v23.742h-19.613zM30.968 4.129h-8.258v-3.097c0-0.569-0.463-1.032-1.032-1.032h-11.355c-0.569 0-1.032 0.463-1.032 1.032v3.097h-8.258c-0.569 0-1.032 0.463-1.032 1.032s0.463 1.032 1.032 1.032h3.097v24.774c0 0.569 0.463 1.032 1.032 1.032h21.677c0.569 0 1.032-0.463 1.032-1.032v-24.774h3.097c0.569 0 1.032-0.463 1.032-1.032s-0.463-1.032-1.032-1.032v0z"
          />
          <path
            class="path2"
            d="M10.323 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          />
          <path
            class="path3"
            d="M16 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          />
          <path
            class="path4"
            d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"
          />
        </symbol>
        <symbol id="icon-clock" viewBox="0 0 32 32">
          <title>clock</title>
          <path
            class="path1"
            fill="#605f5f"
            d="M16 29c-7.168 0-13-5.831-13-13s5.832-13 13-13c7.168 0 13 5.832 13 13s-5.832 13-13 13zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"
          />
          <path
            class="path2"
            fill="#605f5f"
            d="M23.958 21.837l-6.958-6.489v-6.282c0-0.827-0.673-1.5-1.5-1.5s-1.5 0.673-1.5 1.5v6.934c0 0.414 0.174 0.814 0.477 1.098l7.435 6.934c0.279 0.259 0.642 0.402 1.023 0.402 0.415 0 0.814-0.174 1.096-0.477 0.564-0.605 0.532-1.555-0.073-2.12z"
          />
        </symbol>
      </defs>
    </svg>
    <div class="container">
      <div class="cart">
        <div class="page-title-normal">
          <h2 class="page-title-h2">
            <span>购物车</span>
          </h2>
        </div>
        <div class="item-list-wrap">
          <div class="cart-item">
            <div class="cart-item-head">
              <ul>
                <li>产品</li>
                <li>价格</li>
                <li>数量</li>
                <li>合计</li>
                <li>编辑</li>
              </ul>
            </div>
            <ul class="cart-item-list">
              <li v-for="(item,idx) in userCartList" :key="idx">
                <div class="cart-tab-1">
                  <div class="cart-item-check" @click="chooseProduct(item)">
                    <a class="checkbox-btn item-check-btn" v-bind:class="{check:item.checked}">
                      <svg class="icon icon-ok">
                        <use xlink:href="#icon-ok" />
                      </svg>
                    </a>
                  </div>
                  <div class="cart-item-pic">
                    <img v-lazy="'/static/'+item.productImage" v-bind:alt="item.productName" />
                  </div>
                  <div class="cart-item-title">
                    <div class="item-name">{{item.productName}}</div>
                  </div>
                </div>
                <div class="cart-tab-2">
                  <div class="item-price">{{item.salePrice}}</div>
                </div>
                <div class="cart-tab-3">
                  <div class="item-quantity">
                    <div class="select-self select-self-open">
                      <div class="select-self-area">
                        <a class="input-sub" @click="setProductNum(-1,item)">-</a>
                        <span class="select-ipt">{{item.productNum}}</span>
                        <a class="input-add" @click="setProductNum(1,item)">+</a>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="cart-tab-4">
                  <div class="item-price-total">{{(item.productNum*item.salePrice) | currency('￥')}}</div>
                </div>
                <div class="cart-tab-5">
                  <div class="cart-item-opration">
                    <a
                      href="javascript:;"
                      class="item-edit-btn"
                      v-on:click="removeConfirm(item.productId)"
                    >
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del" />
                      </svg>
                    </a>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="cart-foot-wrap">
          <div class="cart-foot-inner">
            <div class="cart-foot-l">
              <div class="item-all-check" @click="chooseAll">
                <a>
                  <span class="checkbox-btn item-check-btn" v-bind:class="{check:selectAll}">
                    <svg class="icon icon-ok">
                      <use xlink:href="#icon-ok" />
                    </svg>
                  </span>
                  <span>全选</span>
                </a>
              </div>
            </div>
            <div class="cart-foot-r">
              <div class="item-total">
                合计:
                <span class="total-price">{{totalPrice | currency('￥')}}</span>
              </div>
              <div class="btn-wrap">
                <a
                  class="btn btn--red"
                  v-bind:class="{'btn--dis':selectCount === 0}"
                  @click="checkout"
                >订单结算</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <model v-bind:modelShow="modelShow" v-on:closeModel="modelDisplay">
      <p slot="modelTips">你确认要删除此条数据吗?</p>
      <a class="btn btn--m" href="javascript:;" slot="modelClose" v-on:click="delcart">确认</a>
      <a
        class="btn btn--m btn--red"
        href="javascript:;"
        slot="modelClose"
        v-on:click="modelDisplay"
      >关闭</a>
    </model>
    <nav-footer></nav-footer>
  </div>
</template>

<style>
.input-sub,
.input-add {
  min-width: 40px;
  height: 100%;
  border: 0;
  color: #605f5f;
  text-align: center;
  font-size: 16px;
  overflow: hidden;
  display: inline-block;
  background: #f0f0f0;
}

.item-quantity .select-self-area {
  background: none;
  border: 1px solid #f0f0f0;
}

.item-quantity .select-self-area .select-ipt {
  display: inline-block;
  padding: 0 3px;
  width: 30px;
  min-width: 30px;
  text-align: center;
}
</style>


<script>
import "../assets/css/checkout.css";
import NavHeader from "../components/NavHeader.vue";
import NavFooter from "../components/NavFooter.vue";
import NavBread from "../components/NavBread.vue";
import model from "../components/model.vue";

import axios from "axios";
import getGoodsCount from "../utils/getCartCount";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      userCartList: null,
      modelShow: false,
      productId: ""
    };
  },

  computed: {
    ...mapState(["userGoodsCount"]),
    totalPrice() {
      let total = 0;
      if (this.userCartList) {
        this.userCartList.forEach(e => {
          if (e.checked) {
            total += e.salePrice * e.productNum;
          }
        });
      }
      return total;
    },

    selectAll() {
      if (this.userCartList) {
        return this.userCartList.every(e => {
          return e.checked === true;
        });
      }
    },

    selectCount() {
      let count = 0;
      if (this.userCartList) {
        this.userCartList.forEach(e => {
          e.checked ? (count += 1) : "";
        });
        return count;
      }
    }
  },

  mounted() {
    this.init();
  },

  updated() {
    // let abc = document.querySelector("html").innerHTML
    // console.log(abc)
  },

  methods: {
    ...mapActions(["setUserGoodsCount"]),
    init() {
      axios.get("/users/cartList").then(res => {
        this.userCartList = res.data.result;
        getGoodsCount(axios, this.setUserGoodsCount);
      });
    },

    modelDisplay() {
      this.modelShow = false;
    },

    removeConfirm(productid) {
      this.modelShow = true;
      this.productId = productid;
    },

    delcart() {
      axios.post("/users/delpd", { productId: this.productId }).then(res => {
        this.modelShow = false;
        this.init();
      });
    },

    setProductNum(val, item) {
      this.productId = item.productId;
      val === 1 ? item.productNum++ : "";
      if (val === -1) {
        if (item.productNum < 2) {
          return (item.productNum = "1");
        }
        item.productNum--;
      }
      axios
        .post("/users/setnum", {
          productId: this.productId,
          productNum: item.productNum
        })
        .then(res => {
          getGoodsCount(axios, this.setUserGoodsCount);
        })
        .catch(err => {
          console.log(err);
        });
    },

    updateCartList(item, flag) {
      axios
        .post("/users/updateCartList", {
          productId: item.productId,
          flag: flag
        })
        .then(response => {
          let res = response.data;
          getGoodsCount(axios, this.setUserGoodsCount);
        });
    },

    chooseProduct(item) {
      item.checked = !item.checked;
      this.updateCartList(item, item.checked);
    },

    chooseAll() {
      let flag = !this.selectAll;
      this.userCartList.forEach(e => {
        e.checked = flag;
        this.updateCartList(e, e.checked);
      });
    },

    checkout() {
      if (this.selectCount === 0) return;
      this.$router.push({ path: "/address" });
    }
  },

  components: {
    NavHeader,
    NavFooter,
    NavBread,
    model
  }
};
</script>




